import React from 'react'
import { Link } from 'react-router-dom'

import logoF from './logo_f.png'


const LogoFooter = () => {
    return (
        <div className="logo-footer">
            <Link to="/home">
                 <img src={logoF} alt=""/>
            </Link>
        </div>        
    )
}

export default LogoFooter