import React from 'react'

import {Route} from 'react-router'

import MainPage from './MainPage/MainPage'
import BlogPage from './BlogPage/BlogPage'
import AboutPage from './AboutPage/AboutPage'
import ContactPage from './ContactPage/ContactPage'
import CategoryPage from './CategoryPages/CategoryPage'

const Main = () => {
    return (
        <>
            <Route path="/home" component={MainPage}/>
                <Route path="/travel" render={() => (
                    <CategoryPage 
                        categoryName="TRAVEL"
                        image–°ategory="/images/travel.png"
                    />
                )}/>
                <Route path="/fashion" render={() => (
                    <CategoryPage 
                        categoryName="FASHION"
                        image–°ategory="/images/fashion.png"
                    />
                )}/>
                <Route path="/lifestyle" render={() => (
                    <CategoryPage 
                        categoryName="LIFESTYLE"
                        image–°ategory="/images/lifestyle.png"
                    />
                )}/>
                <Route path="/food" render={() => (
                    <CategoryPage 
                        categoryName="FOOD"
                        image–°ategory="/images/food.png"
                    />
                )}/>
                <Route path="/inspiration" render={() => (
                    <CategoryPage 
                        categoryName="INSPIRATION"
                        image–°ategory="/images/inspiration.png"
                    />
                )}/>
                <Route path="/photography" render={() => (
                    <CategoryPage 
                        categoryName="PHOTOGRAPHY"
                        image–°ategory="/images/photography.png"
                    />
                )}/>
            <Route path="/blog" component={BlogPage}/> 
            <Route path="/about" component={AboutPage}/>
            <Route path="/contact" component={ContactPage}/>

        </>
    )
}

export default Main
